@import "src/common/style/variables";

@import "src/common/style/mixins";

.modal {
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100vw;
  height: 100vh;

  background-color: rgba(0, 0, 0, 0.4);
}

.content {
  position: relative;

  width: 100%;
  max-width: 395px;
  padding: 20px 25px 47px;

  background-color: $background-base-color;
}

.buttons {
  display: flex;
  justify-content: space-between;

  margin-top: 35px;

}

.button {
  width: 127px;
}

.titleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-bottom: 54px;
}

.title {

  @include headline3;


  margin: 0;

  &::after {
    content: '';

    position: absolute;
    top: 65px;
    left: 0;

    width: 100%;
    height: 1px;

    background-color: #aeaeae;
  }
}

.modalSubtitle {

  @include subtitle;
}

.modalSubtitleColor {
  font-weight: 600;
}

.input {
  width: 100%;
}

.close {
  cursor: pointer;

  padding: 7px;
}

.inputError {
  width: 100%;

  border-bottom: 2px solid red;
}
.checkbox {
  margin-top: 15px;
}